<template>
  <div class="plans-page">
    <!-- Header -->
    <div class="plans-header">
      <button class="plans-header__back" @click="goBack">
        <v-icon icon="mdi-arrow-left" size="24" />
      </button>
      <div class="plans-header__content">
        <h1 class="plans-header__title">Выберите план</h1>
        <p class="plans-header__subtitle">Разблокируйте все возможности</p>
      </div>
    </div>

    <!-- Plans list -->
    <div class="plans-content">
      <div v-if="loading" class="plans-loading">
        <div class="plans-loading__spinner"></div>
      </div>

      <template v-else>
        <transition-group name="fade-up" tag="div" class="plans-list">
          <PlanCard
            v-for="(plan, index) in plans"
            :key="plan.id"
            :plan="plan"
            :is-current="isPlanCurrent(plan)"
            :style="{ transitionDelay: index * 0.1 + 's' }"
            @select="selectPlan(plan)"
          />
        </transition-group>
      </template>
    </div>

    <!-- Features comparison -->
    <div class="plans-features">
      <h3 class="plans-features__title">Сравнение планов</h3>
      <div class="plans-features__table">
        <div class="plans-features__row plans-features__row--header">
          <span></span>
          <span>Free</span>
          <span>Pro</span>
        </div>
        <div class="plans-features__row" v-for="(feature, index) in comparisonFeatures" :key="index">
          <span class="plans-features__label">{{ feature.label }}</span>
          <span class="plans-features__value">{{ feature.free }}</span>
          <span class="plans-features__value plans-features__value--pro">{{ feature.pro }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PlanCard from '@/components/subscription/PlanCard.vue'
import { SubscriptionCall } from '@/api/call'

export default {
  name: 'SubscriptionPlansView',
  components: {
    PlanCard,
  },
  data() {
    return {
      loading: true,
      currentPlanType: 'FREE',
      plans: [],
      comparisonFeatures: [
        { label: 'Страницы', free: '5', pro: '∞' },
        { label: 'Блоки', free: '10', pro: '∞' },
        { label: 'Экспорт', free: '—', pro: '✓' },
        { label: 'Синхронизация', free: '—', pro: '✓' },
        { label: 'Поддержка', free: '—', pro: '✓' },
      ],
    }
  },
  async mounted() {
    await this.loadData()
  },
  methods: {
    async loadData() {
      try {
        this.loading = true
        const [plansResponse, subscriptionResponse] = await Promise.all([
          SubscriptionCall.getPlans(),
          SubscriptionCall.getCurrent(),
        ])
        this.plans = plansResponse.data
        this.currentPlanType = subscriptionResponse.data.plan
      } catch (error) {
        console.error('Failed to load data:', error)
      } finally {
        this.loading = false
      }
    },
    goBack() {
      this.$router.back()
    },
    isPlanCurrent(plan) {
      if (plan.priceStars === 0) {
        return this.currentPlanType === 'FREE'
      }
      return this.currentPlanType === 'PREMIUM'
    },
    selectPlan(plan) {
      if (this.isPlanCurrent(plan)) return
      console.log('Selected plan:', plan)
    },
  },
}
</script>

