<template>
  <div
    class="plan-card"
    :class="{
      'plan-card--premium': isPremium,
      'plan-card--current': isCurrent
    }"
    @click="$emit('select')"
  >
    <!-- Popular badge -->
    <div v-if="plan.popular" class="plan-card__badge">
      <v-icon icon="mdi-fire" size="12" />
      Популярный
    </div>

    <!-- Glow effect for premium -->
    <div v-if="isPremium" class="plan-card__glow"></div>

    <!-- Header -->
    <div class="plan-card__header">
      <div class="plan-card__icon" :class="{ 'plan-card__icon--premium': isPremium }">
        <v-icon :icon="isPremium ? 'mdi-crown' : 'mdi-leaf'" size="24" />
      </div>
      <div class="plan-card__title-group">
        <span class="plan-card__name">{{ plan.name }}</span>
        <span class="plan-card__description">{{ planDescription }}</span>
      </div>
    </div>

    <!-- Price -->
    <div class="plan-card__price">
      <span v-if="plan.priceStars > 0" class="plan-card__price-value">
        {{ plan.priceStars }}
        <span class="plan-card__price-currency">⭐</span>
      </span>
      <span v-else class="plan-card__price-free">Бесплатно</span>
      <span v-if="plan.durationDays" class="plan-card__price-period">/ {{ periodLabel }}</span>
    </div>

    <!-- Features -->
    <ul class="plan-card__features">
      <li
        v-for="(feature, index) in plan.features"
        :key="index"
        class="plan-card__feature"
      >
        <v-icon
          icon="mdi-check-circle"
          size="18"
          class="plan-card__feature-icon"
        />
        <span>{{ feature }}</span>
      </li>
    </ul>

    <!-- Action button -->
    <button
      class="plan-card__button"
      :class="{ 'plan-card__button--premium': isPremium && !isCurrent }"
      :disabled="isCurrent"
    >
      <span v-if="isCurrent">Текущий план</span>
      <span v-else-if="isPremium">Оплатить {{ plan.priceStars }} ⭐</span>
      <span v-else>Начать бесплатно</span>
    </button>

    <!-- Current indicator -->
    <div v-if="isCurrent" class="plan-card__current-indicator">
      <v-icon icon="mdi-check-circle" size="16" />
      Активен
    </div>
  </div>
</template>

<script>
export default {
  name: 'PlanCard',
  props: {
    plan: {
      type: Object,
      required: true,
    },
    isCurrent: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isPremium() {
      return this.plan.priceStars > 0
    },
    periodLabel() {
      const days = this.plan.durationDays
      if (days === 30 || days === 31) return 'месяц'
      if (days === 365 || days === 366) return 'год'
      if (days === 7) return 'неделя'
      return `${days} дн.`
    },
    planDescription() {
      if (this.plan.priceStars === 0) {
        return 'Для начала работы с заметками'
      }
      return 'Полный доступ ко всем функциям'
    },
  },
}
</script>

